import React from 'react'
import ClassNames from 'classnames'
const Steps = ({count,type,direction}) => {
    let steps = []
    for (let index = 0; index < count; index++) {
        steps.push(index)
    }
    return(
        <nav className='content-wrapper'>
          <div className="steps-wrapper" style={{marginRight:direction==='left'?'0px':'150px'}}>
          {steps.map((item,index)=>
               <img src={`static/img/${type}-steps.png`} className={ClassNames({'to-left':direction==='left','to-right':direction==='right'})} key={index} style={{[direction==='left'?'marginRight':'marginLeft']:10*(item*5)}} alt=""/>
          )}
          </div>
          <style jsx>{`
             .steps-wrapper{
                 display: flex;
                 align-items: center;
                 flex-direction: column;
                 margin-top: 20px;
                 margin-bottom: 20px;
             }
             .to-left{
                 width: 50px;
                 opacity: 0.18;
                 margin-top: 15px;
                 transform:rotate(200deg);  
             }
             .to-right{
                width: 50px;
                opacity: 0.18;
                margin-top: 15px;
                transform:rotate(-200deg);  
             }
          `}</style>
        </nav>
      )
}

export default Steps
