import React from 'react'

const HeadOpenSource = () => {
  return (
    <div className='content-wrapper'>
       <div className="head-open-source-wrapper">
            <h3 className="content-text-subheading">
             I frequently contribute 👨‍💻 to the Open Source community, building and sharing my own projects or creating pull request to other repositories.
            </h3>
       </div>
    <style jsx>{`
        .head-open-source-wrapper{
            display:flex;
            flex-direction: column;
            align-items: center;
            margin-bottom: 100px;
            margin-top: 150px;
            padding: 0px 20px 20px;
            border-bottom:2px solid #D6DEF4;
        }
        h3{
          max-width: 830px;
          text-align: center; 
        }
        @media (max-width:800px){
          .head-open-source-wrapper{
            margin-top: 50px;
          }
        }
    `}</style>
    </div>
  )
}

export default HeadOpenSource
