import React from 'react'

const HeadWork = () => {
  return (
    <div className='content-wrapper'>
       <div className="head-work-wrapper">
            <h2 className="content-text-heading">
              Some of my works
            </h2>
            <h3 className="content-text-subheading">
              "The key to e​fficient development is to 'make interesting new mistakes' 😅"
            </h3>
       </div>
    <style jsx>{`
        .head-work-wrapper{
            display:flex;
            flex-direction: column;
            align-items: center;
            margin-bottom: 100px;
            padding: 0px 20px 20px;
            border-bottom:2px solid #D6DEF4;
        }
        h3{
          max-width: 830px;
          text-align: center; 
        }
        @media (max-width:600px){
          .head-work-wrapper{
            margin-bottom: 60px;
          }
        }
    `}</style>
    </div>
  )
}

export default HeadWork
